import {
  Component,
  Input,
  Output,
  EventEmitter,
  ElementRef,
} from '@angular/core';
import { Observable } from 'rxjs';
import { CardStepModel } from '../cards/card-step/card-step.model';

@Component({
  selector: 'kkl-stepper',
  template: (require('./stepper.component.html').default || require('./stepper.component.html')).length ? (require('./stepper.component.html').default || require('./stepper.component.html')) : '',
  styles: [(require('./stepper.component.scss').default || require('./stepper.component.scss')).length ? (require('./stepper.component.scss').default || require('./stepper.component.scss')) : ''],
})
export class StepperComponent {

  @Input() steps: CardStepModel[];

  @Input() steps$: Observable<CardStepModel[]>;

  @Input() direction: string;
  @Input() stepRef: ElementRef;

  @Output() changStep = new EventEmitter<CardStepModel>();

  constructor() { }

  public onStepChange(step: CardStepModel) {
    this.changStep.emit(step);
  }
}
