import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { PaginationInstance } from 'ngx-pagination';

@Component({
  selector: 'app-pagination',
  template: (require('./pagination.component.html').default || require('./pagination.component.html')).length ? (require('./pagination.component.html').default || require('./pagination.component.html')) : '',
  styles: [(require('./pagination.component.scss').default || require('./pagination.component.scss')).length ? (require('./pagination.component.scss').default || require('./pagination.component.scss')) : '']
})
export class PaginationComponent implements OnInit {

  @Input() public pagination: PaginationInstance ;

  @Input() paginationCount: number;
  @Input() maxSize: number;

  @Output() newPage: EventEmitter<number> = new EventEmitter();


  public labels: any = {
    previousLabel: 'הקודם',
    nextLabel: 'הבא',
};

  constructor() { }

  ngOnInit(): void {
  }

  public onPageChange(number) {
    this.pagination.currentPage = number;
  }
}
